#container{
    width:80%;
    margin: auto;
}

body{
    background-size: cover;
    background-image:linear-gradient(#000000b0,#000000b0), url("../../bg.jpg");
    color:#fff;
    background-attachment:fixed;
    text-align: center;
    font-family: 'Noto Sans Nag Mundari', sans-serif;
    font-family: 'Raleway', sans-serif;
}

#heading>h2{
    font-size:48px;
    margin-top:100px;
    margin-bottom: 0px;
    font-weight:bolder;
}

#heading>p{
    font-size:22px;
    margin-top: 5px;
    margin-bottom: 50px;
}

.time{
    display:flex;
    justify-content: center;
}

#days,#hours,#minutes,#second{
    background-color: rgba(76, 76, 75, 0.555);
    width: 100px;
    height:137px;
    margin:0 10px;
    font-weight:bolder;
}

#days>h1,#hours>h1,#minutes>h1,#second>h1{
    font-size:42px;
}

#days::after{
    content: "";
    position: absolute;
    width: 92px;
    border: 3px solid red;
    top: 38%;
    left: 33.2%;
}

#hours::after{
    content: "";
    position: absolute;
    width: 92px;
    border: 3px solid red;
    top: 38%;
    left: 42%;
}

#minutes::after{
    content: "";
    position: absolute;
    width: 92px;
    border: 3px solid red;
    top: 38%;
    left: 50.8%;
}

#second::after{
    content: "";
    position: absolute;
    width: 92px;
    border: 3px solid red;
    top: 38%;
    left: 59.6%;
}

#sub>p{
    margin-top: 90px;
    font-size:22px;
    font-weight:bolder;
}

#sub>input{
    width:260px;
    height:35px;
    position: relative;
    right: 66px;
}

#sub>a{
    display: inline-block;
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    background-color: red;
    position: absolute;
    top: 82%;
    left: 55%;
    padding: 9.5px;
    width: 100px;
}

#icon{
    margin-top:50px;
    font-size:28px;
}

#icon>i{
    margin: 10px;
}