#container{
    width:80%;
    margin:auto;
}

#body{
    margin-top: 100px;
    display: flex;
}

#left{
    width:50%;
}

#left{
    font-size: 30px;
    text-align: left;
}

#left>p{
    font-size: 20px;
    line-height: 30px;
}

#right{
    width:50%;
    font-size: 16px;
    text-align: left;
}

#right>p{
    margin-bottom:30px;
    margin-top: 30px;
}

#right>li{
    list-style:none;
    margin: 10px 0 ;
}

#right>li>i{
    color:red;
    margin-right: 10px;
    font-size:10px;
}